import { Component } from "react";
import { Dispatch } from "redux";
import { connect } from "react-redux";
import { RegistrationData } from "./registration";
import { getRequestAction, setRegistration } from "../store/actions/requests";
import { Business, Request } from "../models/request";
import PartnersData from "./partners";
import { ShareCapitalData } from "./shareCapital";
import EncumbraceData from "./encumbrance";
import { OfficialsData } from "./officials";
import { getQueryString } from "../utils/functions";

const status: any = {
  "00": {
    name: "New Request",
    class: "primary",
    style: {},
  },
  "11": {
    name: "Final",
    class: "success",
    style: {},
  },
  "22": {
    name: "In Progress",
    class: "warning",
    style: {},
  },
  "33": {
    name: "Interim Data",
    class: "info",
    style: {},
  },
  "44": {
    name: "Not Reviewed",
    class: "warning",
    style: {},
  },
};
class DataEntry extends Component<{
  dispatch: Dispatch | any;
  business: Business;
  request: Request;
}> {
  constructor(props: any) {
    super(props);
    this.approve = this.approve.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getRequestAction((data) => {}));
  }
  approve() {
    this.props.dispatch(
      setRegistration({ review: { status: "11" } }, (errors) => {})
    );
  }
  goBack() {
    const url = `/html/company/companydataentry.php?request_id=${getQueryString(
      "request_id"
    )}`;

    window.location.href = url;
  }
  state = {
    conditions: {
      share_capital: ["company", "international"],
      partners: ["clg", "company", "international", "business", "llp"],
      encumbrances: ["company"],
      officials: ["ngo", "cbo", "trust", "society", "sacco"],
      objectives: ["cbo", "ngo", "sacco", "society"],
      member_count: ["cbo"],
    },
  };
  render() {
    return (
      <div className="row">
        <div className="col-xs-12">
          <h3 className="header smaller lighter blue">
            Add Company/Business Registration Details
          </h3>
        </div>
        <h3 className="smaller lighter blue">
          <strong>SEARCH REF: </strong>
          {this.props.request.request_ref_number}
        </h3>
        <div>
          <table
            id="simple-table"
            className="table table-striped table-bordered table-hover"
          >
            <thead>
              <tr>
                <th>Dataset Name</th>
                <th>Registration Number</th>
                <th>Client Name</th>
                <th>Request Package</th>
                <th>Request Date</th>
                <th className="hidden-480">Status</th>
              </tr>
            </thead>
            <tbody>
              {this.props.request.registration_number && (
                <tr>
                  <td>
                    <a href="#">{this.props.request.dataset_name}</a>
                  </td>
                  <td>
                    <a href="#">{this.props.request.registration_number}</a>
                  </td>
                  <td>
                    <a href="#">{this.props.request.client_name}</a>
                  </td>
                  <td>
                    <a href="#">{this.props.request.request_plan}</a>
                  </td>
                  <td>
                    <a href="#">{this.props.request.request_date}</a>
                  </td>
                  <td className="hidden-480">
                    {
                      <span
                        style={status[this.props.request.status].style}
                        className={`label label-sm label-${
                          status[this.props.request.status].class
                        }`}
                      >
                        {status[this.props.request.status].name}
                      </span>
                    }
                  </td>
                </tr>
              )}
            </tbody>
          </table>
          <div className="space-x">
            <button className="btn btn-xs btn-primary" onClick={this.goBack}>
              <i className="ace-icon smaller-80 green" />
              Go Back
            </button>
          </div>
          {this.props.request.registration_number && (
            <>
              <RegistrationData
                registration={this.props.business}
                dispatch={this.props.dispatch}
                conditions={this.state.conditions}
              />
              {this.state.conditions.share_capital.includes(
                String(this.props.business?.type).toString()
              ) && (
                <ShareCapitalData
                  share_capital={this.props.business.share_capital}
                  dispatch={this.props.dispatch}
                />
              )}
              {this.state.conditions.partners.includes(
                String(this.props.business?.type).toString()
              ) && (
                <PartnersData
                  dispatch={this.props.dispatch}
                  partners={this.props.business.partners}
                  type={this.props.business.type}
                />
              )}

              {this.state.conditions.encumbrances.includes(
                String(this.props.business?.type).toString()
              ) && (
                <EncumbraceData
                  dispatch={this.props.dispatch}
                  encumbrances={this.props.business.encumbrances}
                />
              )}
              {this.state.conditions.officials.includes(
                String(this.props.business?.type).toString()
              ) && (
                <OfficialsData
                  dispatch={this.props.dispatch}
                  officials={this.props.business.officials}
                />
              )}
              {String(this.props.request?.status).toString() !== "11" &&
                this.props.business.business_name && (
                  <div
                    style={{ marginLeft: "9px", marginBottom: "3px" }}
                    onClick={this.approve}
                  >
                    <button className="btn btn-sm space-x btn-success">
                      APPROVE
                    </button>
                  </div>
                )}
              <VerificationData request={this.props.request} />
            </>
          )}
        </div>
      </div>
    );
  }
}

const VerificationData = ({ request }: { request: Request }) => (
  <div className="col-lg-12">
    <table
      id="simple-table"
      className="table  table-striped  table-bordered table-hover"
    >
      <tbody>
        <tr>
          <td>
            <strong>REVIEW NOTES:</strong>
          </td>
          <td>
            <span
              className={`label label-sm ${
                request.status === "11" ? "label-success" : "label-warning"
              }`}
            >
              {request.status === "11" ? "APPROVED" : "NOT REVIEWED"}
            </span>
          </td>
          <td>
            <strong>VERIFIED BY:</strong>
          </td>
          <td>{request.verified_by || "-"}</td>
          <td>
            <strong>VERIFIED DATE:</strong>
          </td>
          <td>{request.verified_date || "-"}</td>
        </tr>
      </tbody>
    </table>
  </div>
);

const mapStateToProps = (state: any) => ({
  business: state.request?.business || {},
  request: state.request,
});

export default connect(mapStateToProps)(DataEntry);
