import { Component } from "react";
import { Official } from "../models/request";
import { setRegistration } from "../store/actions/requests";

class _OfficialsData extends Component<{
  dispatch: any;
  officials: Official[] | undefined;
}> {
  state: {
    officials: Official[];
    edited: boolean;
    readonly: boolean;
    errors: object;
    loading: boolean;
  } = {
    officials: [
      { name: "", role: "chairman" },
      { name: "", role: "vice_chairman" },
      { name: "", role: "secretary" },
      { name: "", role: "vice_secretary" },
      { name: "", role: "treasurer" },
      { name: "", role: "vice_treasurer" },
    ],
    edited: false,
    readonly: true,
    loading: false,
    errors: {},
  };
  constructor(props: any) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.toggleEdit = this.toggleEdit.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  get officials() {
    let data: any = {};
    this.state.officials.forEach((i: Official) => {
      data[i.role] = i.name;
    });

    return data;
  }
  componentDidMount() {
    if (this.props.officials)
      this.setState(
        { officials: [...this.state.officials, ...this.props.officials] },
        () => void null
      );
  }
  handleChange(e: any) {
    const [role, name] = [e.target.name, e.target.value];
    const officials = this.state.officials.map((i) => {
      if (i.role !== role) return i;
      else return { ...i, role, name };
    });
    this.setState({ officials, edited: true });
  }
  handleSubmit(e: any) {
    e.preventDefault();
    this.setState({ loading: true, errors: {}, readonly: true }, () => {
      this.props.dispatch(
        setRegistration({ officials: this.state.officials }, (errors: any) => {
          this.setState({ errors, loading: false });
        })
      );
    });
  }
  toggleEdit() {
    this.setState({ readonly: !this.state.readonly });
  }
  render() {
    return (
      <div className="col-lg-12">
        <div className="d-flex align-items-center justify-content-space-between">
          <h3 className=" smaller lighter blue ">
            <strong>OFFICIALS DETAILS: </strong>
          </h3>
        </div>

        <table className="table table-striped table-bordered">
          <thead>
            <tr>
              <th>Role</th>
              <th>Full Name</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>CHAIRMAN</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Chairman's Name"
                  name="chairman"
                  defaultValue={this.officials.chairman}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
            </tr>
            <tr>
              <th>VICE CHAIRMAN</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Vice Chairman's Name"
                  name="vice_chairman"
                  defaultValue={this.officials?.vice_chairman}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
            </tr>
            <tr>
              <th>SECRETARY</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Secretary's Name"
                  name="secretary"
                  defaultValue={this.officials?.secretary}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
            </tr>
            <tr>
              <th>VICE SECRETARY</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Vice Secretary's Name"
                  name="vice_secretary"
                  defaultValue={this.officials?.vice_secretary}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
            </tr>
            <tr>
              <th>TREASURER</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Treasurer's Name"
                  name="treasurer"
                  defaultValue={this.officials?.treasurer}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
            </tr>
            <tr>
              <th>VICE TREASURER</th>
              <td>
                <input
                  type="text"
                  className="form-control form-control-sm"
                  placeholder="Vice Treasurer's Name"
                  name="vice_treasurer"
                  defaultValue={this.officials?.vice_treasurer}
                  onChange={this.handleChange}
                  readOnly={this.state.readonly}
                />
              </td>
              <td rowSpan={6} style={{ borderTop: "none" }}>
                <div className="d-flex justify-content-end">
                  {this.state.edited &&
                  (!this.state.readonly || this.state.loading) ? (
                    <button
                      className="btn btn-sm btn-primary space-x"
                      onClick={this.handleSubmit}
                      disabled={this.state.loading}
                    >
                      {this.state.loading ? "Saving" : "Save"}
                    </button>
                  ) : (
                    <button
                      className="btn btn-sm btn-info space-x"
                      onClick={this.toggleEdit}
                    >
                      Edit
                    </button>
                  )}
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export const OfficialsData = _OfficialsData;

export default OfficialsData;
