import {
  Partner,
  Business,
  Encumbrance,
  Share,
  ShareCapital,
} from "../models/request";

export const ErrorMessage = ({
  errors,
  name,
  className,
}: {
  errors: any;
  name:
    | keyof Business
    | keyof Partner
    | keyof Encumbrance
    | keyof Share
    | keyof ShareCapital
    | "detail";
  className?: string;
}) => (
  <>
    {errors && errors[name] && Array.isArray(errors[name]) ? (
      errors[name].map((i: any) => (
        <p className={`small text-danger my-0 ${className}`}>{i}</p>
      ))
    ) : (
      <p className={`small text-danger my-0 ${className}`}>{errors[name]}</p>
    )}
  </>
);
