import { Component } from "react";
import { Encumbrance } from "../models/request";
import { setRegistration } from "../store/actions/requests";
import { ErrorMessage } from "./abstract";

class _EncumbranceData extends Component<{
  dispatch: any;
  encumbrances?: Encumbrance[];
}> {
  del(payload: Encumbrance) {
    this.props.dispatch(
      setRegistration({ encumbrance: payload, delete: true }, () => void null)
    );
  }
  render() {
    return (
      <div className="col-lg-12">
        <AddEncumbranceData dispatch={this.props.dispatch} />
        <div className="d-flex align-items-center justify-content-space-between">
          <h3 className=" smaller lighter blue ">
            <strong>ENCUMBRANCE DETAILS: </strong>
          </h3>
          <div className="d-flex" style={{ paddingRight: "1rem" }}>
            <button
              data-target="#add-encumbrance"
              data-toggle="modal"
              className="btn btn-sm btn-primary space-x"
            >
              Add
            </button>
          </div>
        </div>
        {this.props.encumbrances && this.props.encumbrances.length > 0 && (
          <table className="table table-striped table-bordered table-hover">
            <thead>
              <tr>
                <th>Description of Evidence</th>
                <th>Date of Evidence</th>
                <th>Secured Amounts</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {this.props.encumbrances?.map((i, index) => {
                const id = `add-encumbrance-${index}`;
                return (
                  <tr>
                    <td>{i?.description_of_evidence || "-"}</td>
                    <td>{i?.date_of_evidence || "-"}</td>
                    <td>{i?.secured_amounts || "-"}</td>
                    <td>
                      <div className="d-flex justify-content-end">
                        <button
                          className="btn btn-info space-x btn-sm"
                          data-target={`#${id}`}
                          data-toggle="modal"
                        >
                          Edit
                        </button>
                        <button
                          className="btn btn-danger space-x btn-sm"
                          onClick={() => this.del(i)}
                        >
                          Del
                        </button>
                      </div>
                      <AddEncumbranceData
                        dispatch={this.props.dispatch}
                        encumbrance={i}
                        id={id}
                        key={id}
                      />
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        )}
      </div>
    );
  }
}

class AddEncumbranceData extends Component<{
  id?: any;
  dispatch: any;
  encumbrance?: Encumbrance;
}> {
  state: { loading: boolean; encumbrance: Encumbrance; errors: object } = {
    loading: false,
    encumbrance: {
      date_of_evidence: "",
      description_of_evidence: "",
      secured_amounts: "",
      pk: null,
    },
    errors: {},
  };
  constructor(props: any) {
    super(props);
    this.setState = this.setState.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.add = this.add.bind(this);
  }
  add() {
    this.validate(() => {
      if (Object.keys(this.state.errors).length > 0) return;
      this.setState({ loading: true }, () =>
        this.props.dispatch(
          setRegistration(
            { encumbrance: this.state.encumbrance, delete: false },
            () => {
              this.setState({ loading: false });
              window.JQuery(".modal").modal("hide");
            }
          )
        )
      );
    });
  }

  validate(callback: any) {
    const errors: any = {};
    if (!this.state.encumbrance.date_of_evidence)
      errors["date_of_evidence"] = "This field is required!";
    if (!this.state.encumbrance.description_of_evidence)
      errors["description_of_evidence"] = "This field is required!";
    if (!this.state.encumbrance.secured_amounts)
      errors["secured_amounts"] = "This field is required!";
    this.setState({ errors }, callback);
  }

  get id() {
    return this.props.id ? this.props.id : "add-encumbrance";
  }
  handleChange(e: any) {
    this.setState({
      encumbrance: {
        ...this.state.encumbrance,
        [e.target.name]: e.target.value,
      },
    });
  }
  handleSubmit(e: any) {
    e.preventDefault();
    this.add();
  }
  componentDidMount() {
    if (this.props.encumbrance)
      this.setState({ encumbrance: this.props.encumbrance });
  }

  render() {
    return (
      <div
        className={`modal  ${this.state.loading ? "in show" : " in fade"}`}
        id={this.id}
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header no-padding">
              <div className="table-header">
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                  id="close-encumbrance-data"
                  disabled={this.state.loading}
                >
                  <span className="white">Ã—</span>
                </button>
                {this.props.encumbrance ? "Edit" : "Add"} Encumbrance Data
              </div>
            </div>

            <div className="modal-body">
              <form action="" onSubmit={this.handleSubmit}>
                <div className="form-group  d-flex">
                  <label className="col-sm-3">Date of Evidence</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Date of Evidence"
                    defaultValue={
                      this.props.encumbrance
                        ? this.props.encumbrance.date_of_evidence
                        : ""
                    }
                    disabled={this.state.loading}
                    onChange={this.handleChange}
                    name="date_of_evidence"
                    required
                  />
                </div>
                <ErrorMessage
                  errors={this.state.errors}
                  name="date_of_evidence"
                />
                <br />
                <div className="form-group  d-flex">
                  <label className="col-sm-3">Description of Evidence</label>
                  <textarea
                    className="form-control"
                    placeholder="Description of Evidence"
                    defaultValue={
                      this.props.encumbrance
                        ? this.props.encumbrance.description_of_evidence
                        : ""
                    }
                    disabled={this.state.loading}
                    onChange={this.handleChange}
                    name="description_of_evidence"
                    required
                  />
                </div>
                <ErrorMessage
                  errors={this.state.errors}
                  name="description_of_evidence"
                />
                <br />
                <div className="form-group  d-flex">
                  <label className="col-sm-3">Amounts Secured</label>
                  <textarea
                    className="form-control"
                    placeholder="Amounts Secured"
                    defaultValue={
                      this.props.encumbrance
                        ? this.props.encumbrance.secured_amounts
                        : ""
                    }
                    disabled={this.state.loading}
                    onChange={this.handleChange}
                    name="secured_amounts"
                    required
                  />
                </div>
                <ErrorMessage
                  errors={this.state.errors}
                  name="secured_amounts"
                />
                <br />
                <div className="modal-footer no-margin-top bg-white">
                  <button
                    className="btn btn-sm btn-danger pull-left"
                    data-dismiss="modal"
                    type="reset"
                    disabled={this.state.loading}
                  >
                    <i className="ace-icon fa fa-times" />
                    Close
                  </button>
                  <button
                    className="btn btn-sm btn-info pull-left"
                    type="submit"
                    disabled={this.state.loading}
                  >
                    <i className="ace-icon fa fa-check" />
                    {this.state.loading ? "Saving" : "Save"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const EncumbranceData = _EncumbranceData;
export default EncumbranceData;
