import { AxiosError, AxiosResponse } from "axios";
import { Dispatch } from "redux";
import { apiGetRequest, apiUpdateRequest } from "../../api";
import { handleApiError } from "../../utils/functions";
import { SET_REQUEST, SET_STATUS } from "../types";

export const setRegistration = (
  payload: any,
  callback: (errors?: any) => void
) => {
  return function (dispatch: Dispatch, getState: any) {
    apiUpdateRequest(payload)
      .then((data: AxiosResponse) => {
        dispatch({
          type: SET_REQUEST,
          payload: data.data,
        });
        callback();
      })
      .catch((error: AxiosError) => {
        callback(handleApiError(error));
      });
  };
};

export const setStatus = (payload: number) => {
  return function (dispatch: Dispatch) {
    dispatch({
      type: SET_STATUS,
      payload,
    });
  };
};

export const getRequestAction = (callback: (data?: any) => void) => {
  return function (dispatch: Dispatch) {
    apiGetRequest()
      .then((data) => {
        dispatch({
          type: SET_REQUEST,
          payload: data.data,
        });
        callback();
      })
      .catch((error) => {
        callback(handleApiError(error));
      });
  };
};
