import { AxiosError } from "axios";

export const normalizeSnakeCase = (text: string) => {
  let _text = String(text);
  let a = _text.split("_").map((_i: string) => {
    let i = _i.split("");
    i[0] = i[0].toUpperCase();
    return i.join("");
  });

  return a.join(" ");
};

export function getQueryString(variable: string, location?: any) {
  if (variable) {
    try {
      let query = location
        ? location.search.substring(1)
        : window.location.search.substring(1);
      let vars = query.split("&");
      for (let i = 0; i < vars.length; i++) {
        let pair = vars[i].split("=");
        if (pair[0] === variable) {
          return pair[1];
        }
      }
    } catch {
      return false;
    }
  }
  return false;
}

export function handleApiError(error: AxiosError) {
  const errors =
    error && error.response?.data
      ? error.response.data
      : { detail: ["A Network Error Occured! Please try again!"] };

  return errors;
}
