import axios from "axios";
import { getQueryString } from "../utils/functions";
axios.defaults.baseURL = process.env.REACT_APP_API_URL;

export const apiGetRequest = () => {
  const [id, module_id] = [
    getQueryString("request_id"),
    getQueryString("moduleid"),
  ];
  return axios.get(`/request/${id}/module/${module_id}/`);
};

export const apiUpdateRequest = (data: Request | any) => {
  const [id, module_id] = [
    getQueryString("request_id"),
    getQueryString("moduleid"),
  ];
  return axios.post(`/request/${id}/module/${module_id}/`, data);
};
