import { Component } from "react";
import { Business } from "../models/request";
import { Dispatch } from "redux";
import { setRegistration } from "../store/actions/requests";
import { ErrorMessage } from "./abstract";

class AddRegistrationData extends Component<{
  registration?: Business;
  dispatch: Dispatch | any;
  conditions?: any;
}> {
  constructor(props: any) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  state: {
    registration: Business;
    loading: boolean;
    errors: object;
    edited: boolean;
  } = {
    registration: {
      business_name: "",
      type: "",
      registration_date: "",
      email: "",
      phone_number: "",
      branches: "",
      physical_address: "",
      postal_address: "",
      kra_pin: "",
      member_count: "",
      objective: "",
      request_ref_number: "",
      registration_number: "",
    },
    loading: false,
    errors: {},
    edited: false,
  };

  componentDidMount() {
    this.setState({
      errors: {},
      registration: { ...this.state.registration, ...this.props.registration },
    });
  }

  validate(callback: any) {
    this.setState({ errors: {} });
    const required: any = [
      "business_name",
      "type",
      "registration_date",
      "registration_number",
    ];
    const errors: any = {};

    required.forEach((i: keyof Business) => {
      const value: any = this.state.registration[i];
      if (value === undefined || value === "" || value === null || !value) {
        if (errors[i]) {
          errors[i].push("This field is required!");
        } else {
          errors[i] = "This field is required!";
        }
      }
    });

    this.setState({ errors }, () =>
      callback && Object.keys(errors).length <= 0 ? callback() : void null
    );
  }
  handleChange(e: any) {
    const initial = () =>
      this.setState({
        registration: {
          ...this.state.registration,
          [e.target.name]: e.target.value,
        },
      });
    if (!this.state.edited)
      this.setState(
        { business: this.props.registration, edited: true },
        initial
      );
    else initial();
  }
  handleSubmit(e: any) {
    e.preventDefault();
    this.validate(() => {
      this.setState({ loading: true });
      // dispatch
      this.props.dispatch(
        setRegistration(
          { registration: this.state.registration },
          (errors: any = {}) => {
            // when the request is done
            setTimeout(
              () =>
                this.setState({ loading: false, errors }, () => {
                  if (Object.values(errors).length === 0)
                    document.getElementById("close-add-reg-data")?.click();
                }),
              1000
            );
          }
        )
      );
    });
  }

  render() {
    return (
      <div id="add-reg-data" className={`modal  fade`} tabIndex={-1}>
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header no-padding">
              <div className="table-header">
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                  id="close-add-reg-data"
                  disabled={this.state.loading}
                >
                  <span className="white">Ã—</span>
                </button>
                {this.props.registration?.business_name ? "Edit" : "Add"}{" "}
                Company/Business Business Data
              </div>
            </div>
            <div className="modal-body padding">
              <div className="text-center">
                <ErrorMessage errors={this.state.errors} name="detail" />
              </div>
              <form onSubmit={this.handleSubmit}>
                <div className="d-flex">
                  <label htmlFor="business_name" className="col-sm-4">
                    Business/Company Name
                  </label>
                  <input
                    type="text"
                    name="business_name"
                    id="business_name"
                    className="form-control"
                    required
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.business_name}
                    placeholder="Company/Business Name (required)"
                    disabled={this.state.loading}
                  />
                  <ErrorMessage
                    errors={this.state.errors}
                    name="business_name"
                  />
                </div>
                <br />
                <div className="d-flex">
                  <label htmlFor="type" className="col-sm-4">
                    Business/Company Type
                  </label>
                  <select
                    name="type"
                    id="type"
                    className="form-control"
                    required
                    onChange={this.handleChange}
                    defaultValue={this.state.registration.type}
                    // value={this.state.registration.type}
                    disabled={this.state.loading}
                  >
                    <option
                      disabled
                      value={""}
                      defaultChecked={!this.props.registration?.type}
                    >
                      {this.props.registration?.type
                        ? `${this.props.registration.type} selected`
                        : "Select Option"}
                    </option>
                    <option value="business">Business</option>
                    <option value="company">Company</option>
                    <option value="cbo">
                      Community Based Organisation (CBO)
                    </option>
                    <option value="trust">Trust</option>
                    <option value="society">Society</option>
                    <option value="sacco">Cooperative SACCO</option>
                    <option value="ngo">
                      Non Govermental Organisation (NGO)
                    </option>
                    <option value="llp">
                      Limited Liability Partnerships (LLP)
                    </option>
                    <option value="clg">
                      Company Limited By Guarantree (CLG)
                    </option>
                    <option value="international">
                      International/Foreign Company
                    </option>
                  </select>

                  <ErrorMessage errors={this.state.errors} name="type" />
                </div>
                <hr />
                <div className="d-flex">
                  <label htmlFor="registration_number" className="col-sm-4">
                    Registration Number
                  </label>
                  <input
                    id="registration_number"
                    type="text"
                    name="registration_number"
                    className="form-control"
                    required
                    placeholder="Registration Number (required)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.registration_number}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage
                    errors={this.state.errors}
                    name="registration_number"
                  />
                </div>
                <br />
                <div className="d-flex">
                  <label htmlFor="registration_number" className="col-sm-4">
                    Registration Date
                  </label>
                  <input
                    id="registration_number"
                    type="text"
                    name="registration_date"
                    className="form-control"
                    required
                    placeholder="Registration Date (required)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.registration_date}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage
                    errors={this.state.errors}
                    name="registration_date"
                  />
                </div>
                <br />
                <div className="d-flex">
                  <label htmlFor="kra_pin" className="col-sm-4">
                    KRA PIN
                  </label>
                  <input
                    type="text"
                    name="kra_pin"
                    id="kra_pin"
                    className="form-control"
                    placeholder="KRA PIN (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.kra_pin}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage errors={this.state.errors} name="kra_pin" />
                </div>
                <hr />
                <div className="d-flex">
                  <label htmlFor="email" className="col-sm-4">
                    Email Address
                  </label>
                  <input
                    type="email"
                    name="email"
                    id="email"
                    className="form-control"
                    placeholder="Email (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.email}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage errors={this.state.errors} name="email" />
                </div>
                <br />
                <div className="d-flex">
                  <label htmlFor="phone_number" className="col-sm-4">
                    Phone Number
                  </label>
                  <input
                    type="text"
                    name="phone_number"
                    id="phone_number"
                    className="form-control"
                    placeholder="Phone Number (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.phone_number}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage
                    errors={this.state.errors}
                    name="phone_number"
                  />
                </div>
                <br />

                <div className="d-flex">
                  <label htmlFor="email" className="col-sm-4">
                    Physical Address
                  </label>
                  <textarea
                    name="physical_address"
                    id="physical_address"
                    className="form-control"
                    placeholder="Physical Address (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.physical_address}
                    disabled={this.state.loading}
                  ></textarea>
                  <ErrorMessage
                    errors={this.state.errors}
                    name="physical_address"
                  />
                </div>
                <br />

                <div className="d-flex">
                  <label htmlFor="email" className="col-sm-4">
                    Postal Address
                  </label>
                  <input
                    type="text"
                    name="postal_address"
                    id="postal_address"
                    className="form-control"
                    placeholder="Postal Address (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.postal_address}
                    disabled={this.state.loading}
                  />
                  <ErrorMessage
                    errors={this.state.errors}
                    name="postal_address"
                  />
                </div>
                <br />

                <div className="d-flex">
                  <label htmlFor="branches" className="col-sm-4">
                    Branches
                  </label>
                  <textarea
                    name="branches"
                    id="branches"
                    className="form-control"
                    placeholder="Branches (optional)"
                    onChange={this.handleChange}
                    defaultValue={this.props.registration?.branches}
                    disabled={this.state.loading}
                  ></textarea>
                  <ErrorMessage errors={this.state.errors} name="branches" />
                </div>

                {this.props.conditions.member_count.includes(
                  String(this.state.registration.type).toString()
                ) && (
                  <>
                    <hr />
                    <div className="d-flex">
                      <label htmlFor="member_count" className="col-sm-4">
                        Number of Members
                      </label>
                      <input
                        type="text"
                        name="member_count"
                        id="member_count"
                        className="form-control"
                        placeholder="Number Of Members (optional)"
                        onChange={this.handleChange}
                        defaultValue={this.props.registration?.member_count}
                        disabled={this.state.loading}
                      />
                      <ErrorMessage
                        errors={this.state.errors}
                        name="member_count"
                      />
                    </div>
                    <br />
                  </>
                )}
                {this.props.conditions?.objectives.includes(
                  String(this.props.registration?.type).toString()
                ) && (
                  <>
                    <hr />
                    <div className="d-flex">
                      <label htmlFor="objective" className="col-sm-4">
                        Objective
                      </label>
                      <textarea
                        name="objective"
                        id="objective"
                        className="form-control"
                        placeholder="Objective (optional)"
                        onChange={this.handleChange}
                        disabled={this.state.loading}
                      ></textarea>
                      <ErrorMessage
                        errors={this.state.errors}
                        name="objective"
                      />
                    </div>
                  </>
                )}
                <br />

                <div className="modal-footer no-margin-top bg-white">
                  <button
                    className="btn btn-sm btn-danger pull-left"
                    data-dismiss="modal"
                    type="reset"
                    disabled={this.state.loading}
                  >
                    <i className="ace-icon fa fa-times" />
                    Close
                  </button>
                  <button
                    className="btn btn-sm btn-info pull-left"
                    type="submit"
                    disabled={this.state.loading}
                  >
                    <i className="ace-icon fa fa-check" />
                    {this.state.loading ? "Saving" : "Save"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export class RegistrationData extends Component<{
  registration: Business;
  dispatch: Dispatch;
  conditions: any;
}> {
  get registration() {
    return this.props.registration;
  }

  render() {
    return (
      <div className="col-lg-12">
        <AddRegistrationData
          registration={this.registration}
          dispatch={this.props.dispatch}
          conditions={this.props.conditions}
        />
        <div className="d-flex align-items-center justify-content-space-between">
          <h3 className=" smaller lighter blue ">
            <strong>REGISTRATION DETAILS: </strong>
          </h3>
          <div className="d-flex" style={{ paddingRight: "1rem" }}>
            <button
              data-target="#add-reg-data"
              data-toggle="modal"
              className="btn btn-sm btn-primary space-x"
            >
              {this.props.registration.business_name ? "Edit" : "Add"}
            </button>
          </div>
        </div>
        <table id="simple-table" className="table table-bordered table-hover">
          <thead>
            <tr>
              <th>#</th>
              <th>Data</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>COMPANY/BUSINESS NAME</th>
              <td>{this.props.registration?.business_name || "-"}</td>
            </tr>
            <tr>
              <th>COMPANY TYPE</th>
              <td>
                {String(this.props.registration?.type || "").toUpperCase() ||
                  "-"}
              </td>
            </tr>
            <tr>
              <th>REGISTRATION NUMBER</th>
              <td>{this.props.registration?.registration_number || "-"}</td>
            </tr>
            <tr>
              <th>REGISTRATION DATE</th>
              <td>{this.props.registration?.registration_date || "-"}</td>
            </tr>
            <tr>
              <th>KRA PIN</th>
              <td>{this.props.registration?.kra_pin || "-"}</td>
            </tr>
            <tr>
              <th>PHONE NUMBER</th>
              <td>{this.props.registration?.phone_number || "-"}</td>
            </tr>
            <tr>
              <th>EMAIL ADDRESS</th>
              <td>{this.props.registration?.email || "-"}</td>
            </tr>
            <tr>
              <th>PHYSICAL ADDRESS</th>
              <td>{this.props.registration?.physical_address || "-"}</td>
            </tr>
            <tr>
              <th>POSTAL ADDRESS</th>
              <td>{this.props.registration?.postal_address || "-"}</td>
            </tr>
            <tr>
              <th>BRANCHES</th>
              <td>{this.props.registration?.branches || "-"}</td>
            </tr>

            {this.props.conditions.member_count.includes(
              String(this.props.registration.type)?.toString()
            ) && (
              <tr>
                <th>NO OF MEMBERS</th>
                <td>{this.props.registration?.member_count || "-"}</td>
              </tr>
            )}
            {this.props.conditions.objectives.includes(
              String(this.props.registration.type).toString()
            ) && (
              <tr>
                <th>OBJECTIVE</th>
                <td>{this.props.registration?.objective || "-"}</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    );
  }
}
