import { Request } from "../../models/request";
import { SET_REGISTRATION, SET_REQUEST } from "../types";

export default function RequestsReducer(
  state: Request | any = {},
  action: { type: string; payload: any }
) {
  switch (action.type) {
    case SET_REGISTRATION:
      return {
        ...state,
        business: {
          ...(state.business ? state.business : {}),
          ...action.payload,
        },
      };
    case SET_REQUEST:
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
}
