import { Component } from "react";
import { Dispatch } from "redux";
import { ShareCapital } from "../models/request";
import { setRegistration } from "../store/actions/requests";
import { ErrorMessage } from "./abstract";

class _ShareCapitalData extends Component<{
  share_capital: ShareCapital[] | undefined;
  dispatch: Dispatch | any;
}> {
  state = {
    loading: false,
  };
  constructor(props: any) {
    super(props);
    this.add = this.add.bind(this);
  }

  add(data: any, delet: any = false) {
    this.setState({ loading: true }, () =>
      this.props.dispatch(
        setRegistration({ share_capital: data, delete: delet }, (errors) => {
          this.setState({ loading: false });
          if (!errors) window.JQuery(".modal").modal("hide");
        })
      )
    );
  }
  render() {
    return (
      <div className="col-lg-12">
        <AddShareCapital
          dispatch={this.props.dispatch}
          add_share_capital={this.add}
          loading={this.state.loading}
        />
        <div className="d-flex align-items-center justify-content-space-between">
          <h3 className=" smaller lighter blue ">
            <strong>SHARE CAPITAL DETAILS: </strong>
          </h3>
          <div className="d-flex" style={{ paddingRight: "1rem" }}>
            <button
              data-target="#add-share-capital"
              data-toggle="modal"
              className="btn btn-sm btn-primary space-x"
            >
              Add
            </button>
          </div>
        </div>
        {this.props.share_capital && this.props.share_capital?.length > 0 && (
          <table
            id="simple-table"
            className="table  table-striped  table-bordered table-hover"
          >
            <thead>
              <tr>
                <th>Name</th>
                <th>Nominal Value</th>
                <th>No of shares</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {this.props.share_capital?.map((i) => {
                const id = `add-share-capital${i.name?.replace(" ", "")}`;
                return (
                  <tr>
                    <td>{i.name || "-"}</td>
                    <td>{i.nominal_value || "-"}</td>
                    <td>{i.number_of_shares || "-"}</td>
                    <td>
                      <div className="d-flex justify-content-end">
                        <button
                          className="btn btn-sm btn-info space-x"
                          data-target={`#${id}`}
                          data-toggle="modal"
                        >
                          Edit
                        </button>
                        <button
                          onClick={() => this.add(i, true)}
                          className="btn btn-sm btn-danger space-x"
                        >
                          Del
                        </button>
                        <AddShareCapital
                          add_share_capital={this.add}
                          dispatch={this.props.dispatch}
                          share_capital={i}
                          id={id}
                          loading={this.state.loading}
                        />
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        )}
      </div>
    );
  }
}

class AddShareCapital extends Component<{
  dispatch: Dispatch;
  share_capital?: ShareCapital;
  add_share_capital: any;
  id?: any;
  loading: boolean;
}> {
  state: { share_capital: ShareCapital; errors: object } = {
    errors: {},
    share_capital: { name: "", nominal_value: 0, number_of_shares: 0 },
  };

  constructor(props: any) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  handleSubmit(e: any) {
    e.preventDefault();
    this.validate(() => {
      if (Object.keys(this.state.errors).length === 0)
        this.props.add_share_capital(this.state.share_capital);
    });
  }
  componentDidMount() {
    if (this.props.share_capital)
      this.setState({ share_capital: this.props.share_capital });
  }
  validate(callback: any) {
    const errors: any = {};
    if (!this.state.share_capital.name)
      errors["name"] = "This Field is required!";
    if (!this.state.share_capital.nominal_value)
      errors["nominal_value"] = "This Field is required!";
    if (!this.state.share_capital.number_of_shares)
      errors["number_of_shares"] = "This Field is required!";
    this.setState({ errors }, () => callback());
  }
  handleChange(e: any) {
    const data = { [e.target.name]: e.target.value };
    this.setState({ share_capital: { ...this.state.share_capital, ...data } });
  }
  get id() {
    return this.props.id ? this.props.id : `add-share-capital`;
  }
  render() {
    return (
      <div className={`modal fade`} id={this.id}>
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header no-padding">
              <div className="table-header">
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                  id="close-add-partners"
                  disabled={this.props.loading}
                >
                  <span className="white">Ã—</span>
                </button>
                {this.props.share_capital ? "Edit" : "Add"} Share Capital Data
              </div>
            </div>

            <div className="modal-body">
              <form action="" onSubmit={this.handleSubmit}>
                <div className="form-group  d-flex">
                  <label className="col-sm-3">Name</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Name"
                    defaultValue={
                      this.props.share_capital
                        ? this.props.share_capital.name
                        : ""
                    }
                    disabled={this.props.loading}
                    onChange={this.handleChange}
                    name="name"
                    required
                  />
                </div>
                <ErrorMessage errors={this.state.errors} name="name" />
                <div className="form-group  d-flex">
                  <label className="col-sm-3">Nominal Value</label>
                  <input
                    type="number"
                    min={0}
                    className="form-control"
                    placeholder="Nominal Value"
                    defaultValue={
                      this.props.share_capital
                        ? this.props.share_capital.nominal_value
                        : ""
                    }
                    disabled={this.props.loading}
                    onChange={this.handleChange}
                    name="nominal_value"
                    required
                  />
                </div>
                <ErrorMessage errors={this.state.errors} name="nominal_value" />
                <div className="form-group  d-flex">
                  <label className="col-sm-3">No of Shares</label>
                  <input
                    type="number"
                    min={0}
                    className="form-control"
                    placeholder="No of Shares"
                    defaultValue={
                      this.props.share_capital
                        ? this.props.share_capital.number_of_shares
                        : ""
                    }
                    disabled={this.props.loading}
                    onChange={this.handleChange}
                    name="number_of_shares"
                    required
                  />
                </div>
                <ErrorMessage
                  errors={this.state.errors}
                  name="number_of_shares"
                  className="floating-error"
                />
                <br />
                <div className="modal-footer no-margin-top bg-white">
                  <button
                    className="btn btn-sm btn-danger pull-left"
                    data-dismiss="modal"
                    type="reset"
                    disabled={this.props.loading}
                  >
                    <i className="ace-icon fa fa-times" />
                    Close
                  </button>
                  <button
                    className="btn btn-sm btn-info pull-left"
                    type="submit"
                    disabled={this.props.loading}
                  >
                    <i className="ace-icon fa fa-check" />
                    {this.props.loading ? "Saving" : "Save"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export const ShareCapitalData = _ShareCapitalData;
